.home-content{
    height: 100vh;
    background: url('/static/visit/cz_bg.png') bottom  no-repeat / contain
}

.home-intro-area{
    margin: 3.5vh 4.5vw 0;
    font-size: 3.9vw;
    line-height:6.4vw;
    color: #333333;
}

.home-btn-group{
    padding: 0 4vw 4.3vh 0;
    position: absolute;
    bottom: 0;
}

.home-btn-item{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;

    width: 44vw;
    margin: 2.9vh 0 0 4vw;
    padding: 3.2vw 0 4.8vw 0;
    background:#ffffff;
    text-align: center;
    font-size: 1.25rem;
    color: #333333;
    border-radius: 4vh;
    -webkit-box-shadow: 0 -4.5pt 4.5pt 0 rgba(46, 96, 101, 0.75) inset;
            box-shadow: 0 -4.5pt 4.5pt 0 rgba(46, 96, 101, 0.75) inset;
}

.home-btn-arrow{
    height: 1.25rem;
    width: 1.25rem;
    margin-left: 0.5rem;
    display: inline-block;
    background: url('/static/visit/cz_black_more.png')  center  no-repeat / contain;
}


