/*两个搜索界面通用的样式*/
.search-content{
    background:url('/static/visit/cz_bg_blur.png') top  no-repeat / contain;
    height: 123vw;
    padding-top: 42vw;
}

.search-input-group{
    padding: 0 4vw;
}

.search-input-item{
    background:#ffffff;
    border:0.5pt solid rgb(117,171,237);
    border-radius:2pt;
    -webkit-box-shadow:0 0 4pt 0 rgba(117, 171, 237, 0.75);
            box-shadow:0 0 4pt 0 rgba(117, 171, 237, 0.75);

    width:100%;
    padding: 2.7vw 0;
}

.search-input-item-title{
    color: rgb(117,171,237);
    border-right: 1.5pt solid rgb(117,171,237);

    height: 8.4vw;
    width: 37.2vw;
    line-height: 8.4vw;
    font-size: 1.2rem;
    text-align: center;
    display: inline-block;
}

.search-confirm-btn{
    background: rgb(117,171,237);
    border-radius: 5pt;
    -webkit-box-shadow:0 0 4pt 0 rgba(117, 171, 237, 0.75);
            box-shadow:0 0 4pt 0 rgba(117, 171, 237, 0.75);
    color: #ffffff;
    text-align: center;

    height: 12.2vw;
    line-height: 12.2vw;
    margin-top: 9.2vw;
    font-size: 1.25rem;
}

/*搜索界面的List样式*/
.search-input-group .am-list-item.am-list-item-middle .am-list-line{
    border-bottom: 0;
}

.search-input-group .am-list-item .am-list-line .am-list-arrow{
    position: absolute;
    right: 5.6vw;
    width: 3.4vw;
    height: 3.4vw;
    background-image: url('/static/visit/cz_black_more.png');
}

.search-input-group .am-list-item .am-list-line .am-list-extra{
    position: absolute;
    right: 10.4vw;
    padding: 0;
}

.search-input-group .am-list-item{
    height: 8.4vw;
    min-height: 8.4vw;
}

.search-input-group .am-list-item.am-input-item{
    height: 8.4vw;
    padding-left: 5vw;
}

.search-input-group .am-list-item .am-input-control input{
    text-align: right;
    padding-right: 5.6vw;
    color: #333333;
    font-size: 1.2rem;
}


