.step1-outer-bg{
    background: #ffffff;
    width: 100%;
}

.step1-inner-bg{
    padding: 4vw 4vw 4.5rem 4vw;
}

.step1-title{
    font-size: 1.45rem;
    font-weight: bold;
    color: #333333;
}

.step1-title span{
    font-size: 1.2rem;
    font-weight: normal;
    color: #666666;
    margin-left: 5.2vw;
}

.step1-bottom-btn{
    position: fixed;
    bottom: 0;
    background: rgb(117, 171, 237);
    height: 3.75rem;
    line-height: 3.75rem;
    width: 100%;
    text-align: center;
    color: #ffffff;
    font-size: 1.25rem;
}

.step1-date-tag-unselectable{
    margin: 0 2.8vw 2.8vw 0;
    width: 27.2vw;
    line-height: 11.2vw;
    text-align: center;
    font-size: 1.1rem;
    color: rgb(153, 153, 153);
    border: 0.3vw solid rgb(125, 125, 125);
    border-radius: 2pt;
}

.step1-date-tag-selected{
    margin: 0 2.8vw 2.8vw 0;
    width: 27.2vw;
    line-height: 11.2vw;
    text-align: center;
    font-size: 1.1rem;
    color: #ffffff;
    background: rgb(117, 171, 237);
    border: 0.3vw solid rgb(117, 171, 237);
    border-radius: 2pt;
}

.step1-date-tag-unselected{
    margin: 0 2.8vw 2.8vw 0;
    width: 27.2vw;
    line-height: 11.2vw;
    text-align: center;
    font-size: 1.1rem;
    color: rgb(117, 171, 237);
    border: 0.3vw solid rgb(125, 125, 125);
    border-radius: 2pt;
}

/*自定义List样式*/
.step1-inner-bg .am-list-item .am-list-line .am-list-content{
    color: #666666;
}

.step1-inner-bg .am-list-item .am-list-line .am-list-extra{
    color: #666666;
}



