.card-list{
    padding: 2vw 4vw 12.3vw;
}

.card-outer{
    width: 100%;
    background: rgb(117, 171, 237);
    -webkit-box-shadow: 0 1pt 4.5pt rgba(46, 96, 101, 0.75);
            box-shadow: 0 1pt 4.5pt rgba(46, 96, 101, 0.75);
    margin-top: 4vw;
    padding-top: 1.3vh;
    border-radius: 2pt;
}

.card-inner{
    background: #ffffff;
    padding: 0.7vh 4vw 4.4vh;
}

.card-inner .am-list-item .am-list-line .am-list-extra{
    overflow: visible;
}


