/*日期*/
.step3-date{
    height: 13.3vw;
    line-height: 13.3vw;
    text-align: center;
    font-size: 1.25rem;
    color: #333333;
    border-bottom: 1.5vh solid rgb(238, 238, 238);
}
/*标题栏*/
.step3-title{
    height: 12vw;
    font-size: 1.25rem;
    color: rgb(117, 171, 237);
    text-align: center;
    border-bottom: 0.3vh solid rgb(117, 171, 237)
}

.step3-title-long{
    width: 55.4vw;
}

.step3-title-short{
    width: 22.3vw;
}
/*一条信息*/
.step3-info{
    padding: 4.9vw 0;
    font-size: 1.2rem;
    color: #333333;
    text-align: center;
    border-bottom: 0.5pt solid rgb(181,181,181)
}

.step3-info-long{
    width: 55.4vw;
}

.step3-info-short{
    width: 22.3vw;
}
/*底部按钮栏*/
.step3-bottom{
    position: fixed;
    bottom: 0;
    height: 3.75rem;
    font-size: 1.25rem;
    line-height: 3.75rem;
    text-align: center;
}

.step3-bottom-back{
    width: 50vw;
    color: rgb(102, 102, 102);
    background: #ffffff;
    border-top: 0.5pt solid rgb(112, 112, 112);
}

.step3-bottom-confirm{
    width: 50vw;
    color: #ffffff;
    background: rgb(117, 171, 237);
    border-top: 0.5pt solid rgb(117, 171, 237);
}


