.success-bottom{
    position: absolute;
    bottom: 0;
    width: 100%;
    color: #333333;
}

.success-number{
    width: 100%;
    font-size: 1.4rem;
    text-align: center;
    margin-bottom: 3.9vh;
}

.success-card{
    margin: 0 4.5vw 2.5vh;
    background: #ffffff;
    border-radius: 2pt;
    -webkit-box-shadow: 0 0 3pt rgba(157, 43, 39, 0.97);
            box-shadow: 0 0 3pt rgba(157, 43, 39, 0.97);
    padding: 2.7vh 4.9vw 3.7vh;
}

.success-btn{
    background: rgb(117, 171, 237);
    margin: 0 4.5vw 2.3vh;
    height: 6.9vh;
    line-height: 6.9vh;
    text-align: center;
    font-size: 1.25rem;
    color: #ffffff;
    border-radius: 5pt;
}

.success-card-title{
    font-size: 1.45rem;
}

.success-card-detail{
    margin-bottom: 3.3vh;
    padding-top: 1rem;
    padding-bottom: 2.7vw;
    font-size: 1.15rem;
    line-height: 1.9rem;
    border-bottom: 1pt solid #e4c6c5;
}

.success-card-hint{
    -ms-flex: 1 1;
        flex: 1 1;
    font-size: 1.15rem;
    line-height: 1.9rem;
    margin-left: 3.4vw;
}

.success-wechat{
    width: 23.5vw;
    height: 23.5vw;
    /* background: url('/static/visit/cz_wechat.png') center no-repeat / contain; */
}


