/*日期*/
.step2-date{
    height: 13.3vw;
    line-height: 13.3vw;
    text-align: center;
    font-size: 1.25rem;
    color: #333333;
    border-bottom: 1.5vh solid rgb(238, 238, 238);
}

/*输入框列表*/
.step2-input-list{
    padding: 4vw 4vw 3.75rem 4vw;
}

.step2-input-outer{
    width: 100%;
    padding-top: 2vw;
    background: rgb(117, 171, 237);
    border: 0.5pt solid rgb(170, 170, 170);
    border-radius: 2pt;
    margin-bottom: 4vw;
}

.step2-input-inner{
    background: #ffffff;
    padding-left: 4.2vw;
}

/*底部按钮栏*/
.step2-bottom{
    position: fixed;
    bottom: 0;
    width: 100vw;
    height: 3.75rem;
    line-height: 3.75rem;
    font-size: 1.25rem;
    text-align: center;
    border-top: 0.5pt solid rgb(112, 112, 112);
    background: #ffffff;
}

.step2-bottom-back{
    width: 50vw;
    height: 100%;
    color: rgb(102, 102, 102);
    border-right: 0.5pt solid rgb(98,98,98);
}

.step2-bottom-next{
    width: 50vw;
    height: 100%;
    color: rgb(102, 102, 102);
}

/*自定义List*/
.step2-input-inner .am-list-item.am-list-item-middle .am-list-line{
    border-bottom: 0;
}

.step2-input-inner .am-list-item .am-list-line .am-list-content{
    padding: 4vw 0 4vw 0.8vw;
}

.step2-input-inner .am-list-item .am-list-line .am-list-extra{
    padding: 4vw 4.4vw 4vw 0;
}

.step2-input-inner .step2-input-picker .am-list-item .am-list-line .am-list-extra{
    padding-right: 0;
}

.step2-input-inner .am-list-item .am-list-line .am-list-arrow{
    margin-left: 0;
    margin-right: 4vw;
    background-image: url('/static/visit/cz_black_more.png');
}

/*自定义List下的InputItem*/
.step2-input-inner .am-list-item.am-input-item{
    padding-left: 0;
    min-height: 0;
    height: 12vw;
}

.step2-input-inner .am-list-item .am-input-label.am-input-label-5{
    padding-left: 0.8vw;
}

.step2-input-inner .am-list-item .am-input-control input{
    text-align: right;
    padding-right: 4vw;
    font-size: 1.2rem;
    color: rgb(51, 51, 51);
}


