.team-textarea-title{
	font-size: 1.2rem;
	color: #333333;
	margin-left: 4vw;
	margin-top: 3vw;
}

.team-textarea-control{
	margin: 3vw 4vw 5rem;
	padding: 1.2rem;
	background: rgba(191, 191, 191, 0.18);
}

.team-textarea{
    color: #000;
    font-size: 1.2rem;
    line-height: 1.6rem;
    width: 100%;
    padding: 0;
    border: 0;
    background-color: transparent;
    overflow: visible;
    display: block;
    resize: none;
    word-break: break-all;
    word-wrap: break-word;
}

.team-check-box-hint{
	font-size: 1rem;
	line-height: 1.2rem;
	color: #999999;
	margin: 1.8vw 4vw;
}

.team-bottom {
	position: fixed;
	bottom: 0;
	width : 100%;
	height: 3.75rem;
	line-height: 3.75rem;
	font-size: 1.25rem;
	text-align: center;
}

.team-bottom-bar-btn{
	display: inline-block;
	
}

.team-bottom-confirm{
	width: 50vw;
	background: rgb(117,171,237);
	border-top: 0.5pt solid rgb(117,171,237);
	color: #ffffff;
}

.team-bottom-clear{
	width: 50vw;
	background: #ffffff;
	border-top: 0.5pt solid rgb(112,112,112);
	color: rgb(102, 102, 102);
}

/*自定义List样式*/
.team-list .am-list-item .am-list-line .am-list-extra{
	color: #999999;
}

.team-list .am-list-item.am-input-item{
	padding-left: 0;
}

.team-list .am-list-item .am-input-label{
	color: #333333;
}

.team-list .am-list-item .am-input-control input{
	text-align: right;
	color: #999999;
	font-size: 1.2rem;
}

.team-list .am-list-item .am-list-line .am-list-arrow{
	background-image: url('/static/visit/cz_grey_more.png');
}

/*自定义date-picker样式*/
.team-list-time .am-list-item .am-list-line .am-list-content{
	padding-bottom: 1.3vw;
}

.team-list-time .am-list-item .am-list-line .am-list-brief{
	margin-top: 0;
	font-size: 1rem;
	color: #999999;
	-o-text-overflow: unset;
	   text-overflow: unset;
}

/*自定义check-box样式*/
.team-list-check .am-list-item .am-list-line .am-list-content{
	padding: 0;
}

.team-list-check .am-list-item .am-list-line .am-list-brief{
	margin-top: 0;
	font-size: 1rem;
	color: #999999;
	-o-text-overflow: unset;
	   text-overflow: unset;
	line-height: 1.2rem;
}

.team-list-check .am-list-item.am-list-item-middle .am-list-line{
	border-bottom: 0;
}

.team-list-check .team-list .am-list-item .am-list-line .am-list-extra{
	padding-top: 0;
}


